import { useState, useCallback, useContext, useEffect } from 'react';
import { DareClientContext } from './LayoutClient'
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function DareSharedQuizCard() {
    const [copied, setCopied] = useState(false);
    const [location, setlocation] = useState("");
    const newQuizDataLocal = localStorage.getItem("newQuizData");



    const DeleteUserData = async () => {
        const response = await fetch(`https://testing.holadares.com/deleteQuiz/${location}`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            }
        })
        if (!response.ok) {
            throw new Error('Data could not be fetched!')
        } else {
            localStorage.clear();
            window.location.reload()
            return response.json()
        }
        
    }


    const onCopy = useCallback(() => {
        setCopied(true);
        toast("Copied")
    }, [])


    useEffect(() => {
        setlocation(window.location.origin + '/en/quiz/' + newQuizDataLocal);
    }, [location]);

    return (
        <div className="App">
            <div className="container-sm">
                <div className="card  col-md-6 offset-md-3 mt-5">
                    <div className="container shadow text-center py-3 border-1-black">
                        <img src="https://dares.in/en/upload/pus.png?v=88" className='max-width-150' alt="challenge header img" />
                        <h3 className="text-danger">Your Challenge is Ready</h3>
                        <h5>Share this link with your friends</h5>
                        <div className="container mt-2">
                            <div className='row'>
                                <div className='col-md-9 col-sm-12 px-0'>
                                    <input className="border btn shadow w-100" type="input" readOnly value={location} />
                                </div>
                                <div className='col-md-3 col-sm-12 mt-2 mt-md-0 px-0'>
                                    <CopyToClipboard onCopy={onCopy} text={location}>
                                        <button className="bg-primary btn  text-white w-100 copy-btn">Copy This Link</button>
                                    </CopyToClipboard>
                                </div>
                            </div>
                        </div>

                        <div className="mt-4 row">
                            <div className="col-md-12 mb-2">
                                <a className="btn btn-success border-radius-100 w-100" href={`https://wa.me/?text=${location}`} target="_blank">
                                    <img src="https://quiz.quotedtale.com/assets/images/wa17.png" alt="whatsapp img" /> <span className="ms-1">Send to WhatsApp </span>
                                </a>
                            </div>

                            <div className="col-md-6 mb-2">
                                <a href={`https://www.snapchat.com/scan?attachmentUrl=${location}`} className="btn border-radius-100 btn-snapchat w-100" target="_blank">
                                    <img className="mr-2" src="https://quiz.quotedtale.com/assets/images/sc17.png" alt="snapchat" /> <span className="ms-1">Snapchat </span> </a>
                            </div>
                            <div className="col-md-6">
                                <a href={`fb-messenger://share?link=${location}`} className="btn border-radius-100 btn-facebook w-100" target="_blank">
                                    <img className="mr-2" src="https://quiz.quotedtale.com/assets/images/msg17.png" alt="fb" /><span className="ms-1">Messenger</span></a></div>
                        </div>


                        <button type="button" className="btn btn-instagram btn-primary border-radius-100 mt-2 mt-md-0" data-bs-toggle="modal" data-bs-target="#instGramModel">
                            <img className="mr-2" src="https://quiz.quotedtale.com/assets/images/in17.png" alt="sd" /> <span className="ms-1">Add to Instagram Bio </span>
                        </button>

                        <div className="modal fade" id="instGramModel" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                            <div className="modal-dialog">
                                <div className="modal-content">
                                    <div className="modal-header">
                                        <h5 className="modal-title" id="exampleModalLabel">How to add this link to your
                                            Instagram Bio.</h5>
                                        <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                                    </div>

                                    <div className="modal-body">
                                        <form>
                                            <div className="form-group">
                                                <label htmlFor="recipient-name" className="col-form-label">Copy Your Link:</label>
                                                <input readOnly type="text" className="form-control" value={location} />
                                            </div>
                                            <div className="form-group">
                                                <label htmlFor="message-text" className="col-form-label">Steps to Follow:</label>
                                                <ol className="list-group list-group-numbered text-start">
                                                    <li className="list-group-item">Copy your link</li>
                                                    <li className="list-group-item">Go on your profile in the app</li>
                                                    <li className="list-group-item">Click on Edit Profile</li>
                                                    <li className="list-group-item">Paste the link under Website section</li>
                                                </ol>
                                            </div>
                                        </form>
                                    </div>

                                    <div className="modal-footer">
                                        <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                                        <a href="https://instagram.com">
                                            <button type="button" className="btn btn-primary">Go to Instagram </button> </a>
                                    </div>

                                </div>
                            </div>
                        </div>


                        <div className="text-center mt-2">
                            <a className="btn btn-warning border-radius-100 btn-block w-100" href={location}>View Your Scoreboard</a>
                        </div>
                        <div className="mt-3">
                            <button className='bg-warning btn' onClick={DeleteUserData}>You can delete this quiz by clicking here</button>
                        </div>
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    );
}

export default DareSharedQuizCard;